import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import OutlinedInput from "@mui/material/OutlinedInput";

const dispatchedEvents = [];

const SrsMuiOutlinedInput = function (props, ref) {
  return (
    <OutlinedInput {...props} theme={props.muiTheme}>
      <slot />
    </OutlinedInput>
  );
};

const styles = `
  /* Custom styles for the MuiOutlinedInput component */
`;

const props = [
  {
    name: "value",
    defaultValue: "",
    type: "string",
  },
  {
    name: "disabled",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "onChange",
    defaultValue: null,
    type: "function",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to OutlinedInput if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiOutlinedInput),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
